@import url(https://fonts.googleapis.com/css?family=Asap|Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.add-button, .form-button, .edit-button {
  height: 4em;
  width: 10em;
  margin-top: 3em;
  background-color: #5C9EAD;
  cursor: pointer;
  font-size: .8em;
  font-family: 'Rubik', sans-serif;
  font-weight: 550;
}

.add-button:hover, .form-button:hover, .edit-button:hover {
  background-color: #E52C20;
}

.edit-button {
  margin-left: 5%;
}

.list-banner {
  width: 60%;
  height: 10%;
  margin-top: 1em;
}

.item-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  -webkit-align-items: center;
          align-items: center;

  -webkit-animation: fadein 1s;
}

.individual-item{
  background-color: #E2D4B7;
  margin-top: .8em;
  padding: .5em;
  width: 80%;
}

.individual-item-link {
  text-decoration: none;
  color: #000;
}

.single-page {
  -webkit-animation: fadein 1s;
}

.single-page-item, .single-page-label {
  margin-left: 5%;
}

.single-page-item {
  font-family: 'Asap', sans-serif;
}

.single-page-label {
  font-weight: bold;
  font-family: 'Rubik', sans-serif;
  font-size: 1.2em;
}

.form {
  text-align: center;
  margin-top: 3em;

  -webkit-animation: fadein 1s;
}

.form-input {
  height: 3em;
  width: 18em;
  margin-top: .5em;
  font-size: .8em;
  padding-left: 1em;
  background-color: #E2D4B7;
  border: none;
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@media only screen and (min-width: 760px) {
  .individual-item {
    width: 65%;
  }

  .add-button, .form-button, .edit-button {
    font-size: 1em;
  }
}



.header {
    background-color: #326273;
    padding: 1em;
}

.nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.link {
    text-decoration: none;
    color: #000;
    cursor: pointer;
    border-bottom: 1px solid #326273;
    font-family: 'Rubik', sans-serif;
}

.link:hover {
    border-bottom: 1px solid #000;
}
.home {
    max-width: 80%;
    margin: 0 auto;
    text-align: center;
}

.heading {
    font-family: 'Rubik', sans-serif;
    font-size: 3em;
    margin: 1em 0 1em 0;
}

.heading-image {
    width: 50%
}

.welcome-text {
    line-height: 1.4em;
    margin: 3em 0 2em 0;
}

.welcome-button {
    height: 12em;
    width: 12em;
    margin: 5em 1em 3em 1em;
    cursor: pointer;
    background-color: #5C9EAD;
    border-radius: 8%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-family: 'Rubik', sans-serif;
}

.welcome-button:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

@media only screen and (min-width: 760px) {
    .home {
        width: 40%;
    }

    .welcome-button {
        height: 16em;
        width: 16em;
        font-size: .8em;
    }
}
.name {
    font-size: 2em;
    font-family: 'Rubik', sans-serif;
}

.ingredients {
    font-size: 1em;
    font-family: 'Asap', sans-serif;
}


.activity {
    font-size: 2em;
    font-family: 'Rubik', sans-serif;
}

.description {
    font-size: 1em;
    font-family: 'Asap', sans-serif;
}


